import React, { useState, useEffect } from "react";

const facts = [
  "zielony, ty i czerwony to barwy panafrykaskie (cho niektrzy uwaaj e czarny zamiast tego)",
  "flaga Litwy wykorzystuje barwy panafrykaskie",
  "barwy pansowiaskie to niebieski, biay i czerwony",
  "barwami panarabskimi s barwy czerwona, zielona, biaa i czarna",
  "krzy nordycki wystpuje tylko w flagach nordyckich",
  "flaga Krlestwa Francji bya caa biaa",
  "do 2011 roku flaga Libii skadaa si wycznie z zielonej barwy",
];

const LoadingBox = () => {
  const [count, setCount] = useState(3);
  const [fact, setFact] = useState("");

  useEffect(() => {
    const randomIndex = Math.floor(Math.random() * facts.length);
    setFact(facts[randomIndex]);
  }, []);

  useEffect(() => {
    if (count > 0) {
      const timer = setTimeout(() => setCount(count - 1), 1000);
      return () => clearTimeout(timer);
    }
  }, [count]);

  return (
    <div className="main-game__loading">
      <p className="main-game__loading__count">{count}</p>
      <div className="main-game__loading__fact">
        <p className="main-game__loading__fact-title">Czy wiesz, e...</p>
        <p className="main-game__loading__fact-text">{fact}</p>
      </div>
    </div>
  );
};

export default LoadingBox;
